body {
  margin: 0;
  background-color: black;
}

.star {
  position: absolute;
  width: 1px;
  height: 1px;
  background-color: white;
}

/* Set the animation, color, size and hide the text */
.intro {
    top: 30%;
    z-index: 1;
    animation: intro 5s ease-out 1s;
    color: rgb(75, 213, 238);
    font-size: 18pt;
    font-weight: lighter;
    opacity: 0;
    min-height: 50vh;
    margin-left:10%;
}

@keyframes intro {
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}


/* Set the animation & hide the logo */
.logo {    
  position: absolute;
  top: 20%;
  left: 45%;
  z-index: 1;
  margin: auto;
  animation: logo 30s ease-out ;
  opacity: 0;
}

.logo svg {
  width: inherit;
}

/* Scale the logo down and maintain it centered */
@keyframes logo {
    0% {
        width: 18em;        
        transform: scale(2.75);
        opacity: 1;
    }
    50% {
        opacity: 1;
        width: 18em;      
    }
    100% {
        opacity: 0;
        transform: scale(0.1);
        width: 18em;
    }
}


p {
  color: #FFFF82;
}

/* Set the font, lean the board, position it */
#board {
  font-family:" Century Gothic, CenturyGothic, AppleGothic, sans-serif";
  transform: perspective(300px) rotateX(25deg);
  transform-origin: 50% 100%;
  text-align: justify;
  position: absolute;
  margin-left: -9em;
  font-weight: bold;
  overflow: hidden;
  font-size: 350%;
  height: 50em;
  width: 18em;
  bottom: 0;
  left: 50%;

}

#board:after {    
  position: absolute;
  content: ' ';
  bottom: 20%;
  left: 0;
  right: 0;
  top: 0;
 
}

/* Set the scrolling animation and position it */
#content {
  animation: scroll 130s linear 16s;
  position: absolute;
  top: 100%;
  
}

#title, #subtitle {
  text-align: center;
}

@keyframes scroll {
    0% {
        top: 100%;
    }
    100% {
        top: -170%;
    }
}


